<template>
  <div id="Menu">
    <h1>My User Manager</h1>
    <br>
    <h2>Users in database : </h2>
    <p v-for="(_user, index) in users" :key="index">- {{_user.name}} ({{_user.email}}) <button @click="removeUser(index)">Remove</button></p>
    <br>
    <p>Name: <input type="text" v-model="user.name"></p>
    <p>E-mail: <input type="email" v-model="user.email"></p>
    <button @click="addUser()">Add User</button>
  </div>
</template>

<script>

import {getUsers, postUser, deleteUser} from './services/UsersServices.js'

export default {
  name: 'Menu',
  data () {
    return {
      users: [],
      user: {
        name: "",
        email: ""
      },
    }
  },

  methods: {
    addUser: async function(){
      if(this.user.name !== "" && this.user.email !== "") {
        this.users = await postUser(this.user)
        this.user.name = ""
        this.user.email = ""
      } else {
        alert('No field can be empty...')
      }
    },

    removeUser: async function(id) {
      this.users = await deleteUser(id);
    }
  },

  async created () {
    this.users = await getUsers();
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



