import {HTTP} from './HTTP.js'

async function getUsers() {
    var response;
    await HTTP.get('users')
    .then(res => {
        response = res.data
    })
    .catch(err =>{
        alert(err)
    })

    return response;
}

async function postUser(user) {
    var response;
    await HTTP.post('user', user)
    .then(res => {
        response = res.data;
    })
    .catch(err => {
        alert(err)
    })

    return response;
}

async function deleteUser(id) {
    var response;
    await HTTP.delete('user/' + id)
    .then(res => {
        response = res.data;
    })
    .catch(err => {
        alert(err)
    })

    return response;
}

export {getUsers, postUser, deleteUser};


